<template>
  <div
    class="page-news"
    :class="{ 'is-popup-showing': isPopupShowing }"
  >
    <transition
      name="transition-header"
      :duration="{ enter: 2000, leave: 600 }"
    >
      <div
        v-if="isPopupShowing"
        class="page-news__header"
      >
        <the-header-inner/>
      </div>
    </transition>

    <div class="page-news__root">
      <div id="page-news__wrapp" ref="contentWrap" class="page-news__wrapp">
        <the-header-new />

        <div class="page-news__main">

        <div class="page-news__main-wrapper">
          <div class="page-news__main-content">
            <div class="page-news__heading">DMEX News</div>
            <div class="page-news__date">Today <span>{{today()}}</span></div>
              <div class="page-news__list">
                <router-link :to="{ name: 'futures-contract' }" class="page-news__list-item">
                  <div class="page-news__list-item-point">
                    <e-icon :src="iconTools"/>
                  </div>
                  <div class="page-news__list-date">
                    June 12, 2019
                  </div>
                  <div class="page-news__list-headline">
                    The first Futures Contract has officially launched on DMEX!
                  </div>
                  <div class="page-news__list-text">
                    Symbol: ETHUSD
                    <br>
                    Margin: 5x
                    <br>
                    Expires: 1 July
                  </div>
                  <div class="page-news__list-readmore">
                    Read more
                    <div class="page-news__list-readmore-icon">
                      <e-icon :src="iconArrow"/>
                    </div>
                  </div>
                </router-link>
                <router-link :to="{ name: 'dark-mode' }" class="page-news__list-item">
                  <div class="page-news__list-item-point">
                    <e-icon :src="iconTools"/>
                  </div>
                  <div class="page-news__list-date">
                    March 04, 2019
                  </div>
                  <div class="page-news__list-headline">
                    Dark Mode now available
                  </div>
                  <div class="page-news__list-text">
                    The possibility to switch between Light and Dark Mode is now live on the UI.
                  </div>
                  <div class="page-news__list-readmore">
                    Read more
                    <div class="page-news__list-readmore-icon">
                      <e-icon :src="iconArrow"/>
                    </div>
                  </div>
                </router-link>
                <router-link :to="{ name: 'tradingview' }" class="page-news__list-item">
                  <div class="page-news__list-item-point">
                    <e-icon :src="iconWarning"/>
                  </div>
                  <div class="page-news__list-date">
                    December 14, 2018
                  </div>
                  <div class="page-news__list-headline">
                    TradingView Chart
                  </div>
                  <div class="page-news__list-text">
                    The TradingView chart is now integrated and live. This change should considerably improve your trading experience.
                  </div>
                  <div class="page-news__list-readmore">
                    Read more
                    <div class="page-news__list-readmore-icon">
                      <e-icon :src="iconArrow"/>
                    </div>
                  </div>
                </router-link>
                <router-link :to="{ name: 'postsecond' }" class="page-news__list-item">
                  <div class="page-news__list-item-point">
                    <e-icon :src="iconTools"/>
                  </div>
                  <div class="page-news__list-date">
                    September 13, 2018
                  </div>
                  <div class="page-news__list-headline">
                    Ledger Nano S wallet now supported
                  </div>
                  <div class="page-news__list-text">
                    We are happy to announce that Ledger Nano S wallet is now supported on EtherMium! Hardware wallets provide an additional level of security for users. Your private keys are stored on a device without an internet connection. When you wish to perform a transaction, the transaction data is sent to the hardware device, where it is signed and returned to the application — this way your private key is never known outside of the hardware device. Trade safely from your hardware wallet!
                  </div>
                  <div class="page-news__list-readmore">
                    Read more
                    <div class="page-news__list-readmore-icon">
                      <e-icon :src="iconArrow"/>
                    </div>
                  </div>
                </router-link>
                <div class="page-news__list-line"></div>
              </div>
            <div class="page-news__date">Project launch <span>November 19. 2017</span></div>
          </div>
        </div>
      </div>

        <the-footer/>
      </div>
    </div>

    <!-- popup and menu -->
    <the-popup v-if="isPopupShowing"/>
    <the-menu-sidebar v-if="isPopupShowing"/>
    <!-- popup and menu -->
  </div>
</template>

<script>
/**
 * Component: Страница новостей
 *
 * @version 2.0.0
 */


import { mapGetters } from 'vuex';

import iconArrow from '@/icons/e-icon_arrow.svg';
import iconWarning from '@/icons/e-icon__warning.svg';
import iconMore from '@/icons/e-icon__more.svg';
import iconTools from '@/icons/e-icon__tools.svg';

// Components
import EBigNumber from '@/components/EBigNumber';
import EButton from '@/components/EButton';
import EIcon from '@/components/EIcon';
import TheFooter from '@/containers/blocks/TheFooter';
import TheHeaderInner from '@/containers/blocks/TheHeaderInner';
import TheHeaderNew from '@/containers/blocks/TheHeaderNew';
import TheMenuSidebar from '@/containers/blocks/TheMenuSidebar';
import ThePopup from '@/containers/blocks/ThePopup';
import TheMobileTokenSearch from '@/containers/blocks/TheMobileTokenSearch';
import TheTokenSearchResultNew from '@/containers/blocks/TheTokenSearchResultNew';
import TheTokenSearchEtherscanMobile from '@/containers/blocks/TheTokenSearchEtherscanMobile';

export default {
  name: 'page-news',

  data() {
    return {
      iconArrow,
      iconWarning,
      iconMore,
      iconTools,
      today: () => {
        const monthNames = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];
        const date = new Date();
        return `${monthNames[date.getMonth()]} ${date.getDate()}, ${date.getFullYear()}`;
      },
    };
  },

  computed: {
    ...mapGetters({
      isPopupShowing: 'ui/isPopupShowing',
    }),
  },

  async mounted() {
    this.$ga.page(this.$router);
  },

  components: {
    TheTokenSearchEtherscanMobile,
    TheMobileTokenSearch,
    TheTokenSearchResultNew,
    EBigNumber,
    EButton,
    EIcon,
    TheFooter,
    TheHeaderInner,
    TheHeaderNew,
    TheMenuSidebar,
    ThePopup,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.page-news {
  $this: &;

  &__root {
    font-weight: 500;
    background-color: #f2f2f2;
    width: 100%;
    height: 100vh;
    overflow: hidden;
  }

  &__wrapp {
    overflow-y: auto;
    overflow-x: hidden;
    width: 100%;
    height: 100%;
  }

  &__header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 78px;
    z-index: 102;
  }

  // MAIN section
  // ==================================================
  &__main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #031472;
    position: relative;
    margin-top: -120px;
    padding-top: 120px;
    z-index: 0;

    &::after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      z-index: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(to bottom, rgba(5,4,26,.5) 0%,rgba(5,4,26,.4) 30%, rgba(5,4,26,0) 70%, rgba(5,4,26,0) 100%);
    }

    &::before {
      content: '';
      position: absolute;
      left: 50%;
      transform: translate(-50%, 0);
      z-index: 0;
      bottom: -120px;
      width: 100%;
      max-width: 1600px;
      height: 180px;
      background: #030C52;
      border-radius: 50%;
      filter: blur(30px);
      opacity: .4;

      @include r(992) {
        display: none;
      }
    }
  }

  &__main-wrapper {
    background: #f2f2f2;
    z-index: 1;
  }

  &__main-content {
    width: 100%;
    max-width: 1440px;
    padding: 70px 230px 90px;
    box-sizing: border-box;
    margin: 0 auto;
    position: relative;

    @include r(1220) {
      padding: 70px 130px 90px;
    }

    @include r(992) {
      padding: 50px 40px;
    }

    @include r(640) {
      padding: 30px 15px;
    }
  }

  &__heading {
    font-size: 34px;
    line-height: 44px;
    letter-spacing: -.4px;
    padding-bottom: 24px;
    text-align: center;
    color: #0d105b;
  }

  &__date {
    text-transform: uppercase;
    color: #bbc2d0;
    text-align: center;
    font-size: 13px;
    font-weight: normal;
    line-height: 19px;

    @include r(768) {
      display: none;
    }

    span {
      display: block;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    position: relative;
    margin: 20px 0 26px;

    @include r(768) {
      flex-direction: column;
      justify-content: flex-start;
    }

    @include r(640) {
      margin: 0;
    }

    &-line {
      display: block;
      content: '';
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      width: 1px;
      height: 100%;
      background: #dee3ec;
      z-index: -2;

      @include r(768) {
        display: none;
      }

      &::before, &::after {
        display: block;
        position: absolute;
        content: '';
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background: #dee3ec;
        left: -4px;
      }

      &::before {
        top: -9px;
      }

      &::after {
        bottom: -9px;
      }
    }

    a {
      text-decoration: none;
    }
  }

  &__list-item {
    display: inline-block;
    cursor: pointer;
    background: #fff;
    box-shadow: 0 5px 9px rgba(31, 46, 233, .08);
    transition: all .2s ease-in-out;
    padding: 34px 24px;
    box-sizing: border-box;
    border-radius: 5px;
    width: calc(50% - 100px);
    margin: 29px 0;
    position: relative;

    @include r(768) {
      width: 100%;
      margin: 10px 0;
    }

    &::after {
      display: block;
      content: '';
      position: absolute;
      height: 1px;
      width: 100px;
      z-index: -1;
      background: #dee3ec;
      top: 50%;
      transform: translateY(-50%);

      @include r(768) {
        display: none;
      }
    }

    &-point {
      display: block;
      position: absolute;
      content: '';
      width: 30px;
      height: 30px;
      border-radius: 50%;
      top: 50%;
      transform: translateY(-50%);
      box-shadow: 0 5px 9px rgba(31, 46, 233, 0.08);
      background: #fff;

      @include r(768) {
        display: none;
      }

      svg {
        width: 17px;
        left: 50% !important;
        top: 50% !important;
        transform: translate(-50%, -50%);
      }
    }

    &:nth-child(odd) {
      &::after {
        right: -100px;
      }

      #{$this}__list-item-point {
        right: -115px;
      }

      &:hover {
        transform: translate(0, -5px);
      }
    }

    &:nth-child(even) {
      transform: translateY(60%);

      @include r(768) {
        transform: translateY(0);
      }

      &::after {
        left: -100px;
      }

      #{$this}__list-item-point {
        left: -115px;
      }

      &:hover {
        transform: translate(0, 58%);

        @include r(768) {
          transform: translate(0, -5px);
        }
      }
    }

    &:last-of-type {
      margin-bottom: 70px;

      @include r(768) {
        margin-bottom: 0;
      }
    }

    &:hover {
      background: #F5F4F4;
      box-shadow: 0 15px 14px rgba(205, 226, 245, .6);

      #{$this}__list-date {
        color: #1F2EE9;
      }
      #{$this}__list-text {
        color: #1F2EE9;

        &::after {
          background-image: linear-gradient(180deg, rgba(238, 238, 238, 0) 0%, #f5f4f4 83%, #f5f4f4 100%);
        }
      }
      #{$this}__list-readmore {
        color: #1F2EE9;
      }
      #{$this}__list-readmore-icon svg path {
        fill: #1F2EE9;
      }
    }
  }

  &__list-date {
    color: #BBC2D0;
    font-weight: normal;
    font-size: 13px;
    letter-spacing: .5px;
    line-height: 15px;
    padding-bottom: 20px;
    text-transform: uppercase;
    transition: color .15s ease-in-out;
  }

  &__list-headline {
    color: #1F2EE9;
    font-size: 23px;
    line-height: 25px;
    font-weight: 500;
    padding-bottom: 24px;
    transition: color .15s ease-in-out;

    @include r(640) {
      padding-bottom: 8px;
    }
  }

  &__list-text {
    position: relative;
    font-size: 16px;
    line-height: 24px;
    font-weight: 500;
    color: #000;
    opacity: .6;
    height: 144px;
    overflow: hidden;
    transition: color .15s ease-in-out;
    margin-bottom: 16px;
    z-index: 1;

    @include r(640) {
      font-size: 13px;
      font-weight: normal;
    }

    &::after {
      content: '';
      left: 0;
      bottom: -8px;
      width: 100%;
      height: 68px;
      z-index: 2;
      position: absolute;
      background-image: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #ffffff 83%, #ffffff 100%);
    }
  }

  &__list-readmore {
    color: #070337;
    font-size: 16px;
    font-weight: 500;
    display: flex;
    justify-content: space-between;
    transition: color .15s ease-in-out;
  }

  &__list-readmore-icon {
    width: 12px;
    height: 15px;
    transform: rotate(-90deg) translate(-7px, 0);

    svg path {
      fill: #00E698;
      transition: fill .15s ease-in-out;
    }
  }
}

</style>



